*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.parent{
    width:95%;
    margin: auto;
    height:1800px;
    /* background-color: bisque; */
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.parent h1{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color:rgb(32, 30, 30);
    height: 100px;
}

.parent .card{
    width:calc(95%/4);
    height: 320px;
    /* background-color: aqua; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    border:2px solid  #EC407A;
    border-radius: 20px;
    align-items: center;
}
.parent .card p{
    padding: 5px;
    font-size: 14px;;
}
.parent .card a button{
    border: none;
    width:200px;
    background-color: #EC407A;
    padding: 10px;
    border-radius: 30px;
    color: white;
    font-size: 16px;
    cursor: pointer;

}

.parent .card button:hover{
    background-color: white;
    border:3px solid #EC407A;
    color: #EC407A;
    font-family: 600;
}

.parent .card img{
    width:90%;
    height:200px;

}
@media(min-width:900px)and (max-width:1160px){
    .parent{
        height:1900px;
    }
    
    .parent .card{
        height: 350px;
    }
    .parent .card a button{
        width:150px;
    }
    
}
@media(min-width:600px)and (max-width:899px){
    .parent{
        width:95%;
        height:3000px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    
    .parent .card{
        width:calc(95%/3);

        height: 380px;
    }
    .parent .card a button{
        width:130px;
    }

}
@media(min-width:400px)and (max-width:599px){
    .parent{
        width:95%;
        height:4000px;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .parent .card{
        width:calc(95%/2);
        height: 380px;
    }
    .parent .card a button{
        width:130px;
    }

}

