*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.parent{
    width:100%;
    height:500px;
    background-color: rgb(40, 35, 35);
    color: white;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: center;
}
.parent .contact{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width:350px;
    height:250px;
    /* background-color: antiquewhite; */
}
.parent .icon{
    display: flex;
}
.parent .icon p{
    margin: 0px 10px;
}
.parent .contact h3,
.parent .contact .social{
    display: flex;
    width:100%;
}
.parent .contact .icon img{
    width:30px;
    height:30px;
    border-radius: 20px;
}
.parent .contact .social img{
    width:30px;
    height:30px;
    margin:0px 3px;
}

.parent .links {
    display: flex;
    flex-direction: column;
    width:250px;
    height:250px;
    justify-content: space-around;
    /* background-color: red; */
}
.parent h3{
    width:100%;
    display: flex;

}
.parent  a{
    text-decoration: none;
    color: white;
    display: flex;
    justify-content: flex-start;
}
.parent  a:hover{
    color: #EC407A;
    transition: 0.5s;
}
.parent .acc{
    display: flex;
    flex-direction: column;
    width:250px;
    height:250px;
    justify-content: space-around;
    /* background-color: gold; */
}
.parent .sub{
    display: flex;
    flex-direction: column;
    /* background-color: red; */
    width:400px;
    height:300px;
    justify-content: space-between;
    padding: 40px 20px;
}
.parent .sub .row input{
    border: none;
    height:50px;
    width:300px;
    border-radius: 50px;
    font-size: 16px;
}
.parent .sub .row button{
    position: relative;
    right:50px;
    border: none;
    background-color: #EC407A;
    color: white;
    padding: 13px;
    cursor: pointer;
    border-radius: 30px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 16px;
    font-weight: 600;
}
@media(min-width:1100px)and (max-width:1270px){
    .parent .contact{
        width:300px;
    }
    .parent .links {
        width:200px;
    }
    .parent .acc{
        width:200px;
    }
    .parent .sub{
        width:350px;
        display: flex;
        height:320px;
        justify-content: space-between;
    }
    .parent .sub .row button{
        position: relative;
        left:125px;
        bottom: 47px;
    }

}
@media(min-width:980px)and (max-width:1099px){
    .parent .contact{
        width:250px;
    }
    .parent .links {
        width:200px;
    }
    .parent .acc{
        width:200px;
    }
    .parent .sub{
        width:300px;
        display: flex;
        height:320px;
        justify-content: space-between;
    }
    .parent .sub .row input{
        width:250px;
    }
    .parent .sub .row button{
        position: relative;
        left:100px;
        bottom: 47px;
    }

}
