*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.succ{
    font-size: 20px;
    color: rgb(22, 204, 22);
}
.wrong{
    font-size: 20px;
    color:rgb(226, 49, 49);
}
.parent{
    height:500px;
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    /* background-color: aqua; */
}

.parent .card{
    display:flex;
    width:80%;
    /* background-color: bisque; */
}
.parent .card .words{
    width:90%;
    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    /* background-color: brown; */
    margin-left: 30px;
}
.parent .card .words h2{
    color:#EC407A;
}
.parent .card img{
    width:300px;
    height:350px;
}
.parent .card .actions{
    width:90%;
    height:20%;
    /* background-color: #EC407A; */
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.parent .card .actions button{
    width:150px;
    height:60%;
    border:none;
    margin: 0px 10px;
    background-color: #EC407A;
    color:white;
    font-size: 16px;
    font-weight:600 ;
    border-radius: 20px;
    cursor: pointer;
}
.parent .card .actions button:hover{
    background-color: white;
    border:2px solid #EC407A;
    color: #EC407A;
    transition: 0.5s;
}



