@import url('https://fonts.googleapis.com/css2?family=Whisper&display=swap');
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
nav{
    display: flex;
    flex-direction: column;
    width:100%;
    height:130px;
    z-index: 999;
   
}
nav a{
    text-decoration: none;
    color:black;
}

nav a:hover{
    color:#EC407A;
    transition: 0.3s;
}

nav .up img{
    width:25px;
    height:25px;
    object-fit: cover;
}
nav .up{
    display: flex;
    margin: auto;
    width:80%;
    /* background-color: aqua;  */
    height:40%;
    align-items: center;
    justify-content:space-between;
    font-size: 18px;
}

nav .up .call{
    display: flex;
    width:300px;
    height: 100%;
    padding: 4px;
    /* background-color: brown; */
    margin-left: 30px;
    align-items: center;
}

nav .up .right{
    display: flex;
    width:200px;
    /* background-color: burlywood; */
    height: 100%;
    justify-content: space-around;
}
nav .up .right .wish,
nav .up .right .login{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
}
nav .up .right .wish a,
nav .up .right .login a{
    padding-left: 5px;
}
nav .line{
    width:100%;
    border:0.5px solid rgba(128, 128, 128, 0.226);
}

nav .down{
    display: flex;
    margin: auto;
    width:80%;
    height:70%;
}
nav .down .logo{
    display: flex;
    height:100%;
    justify-content: center;
    align-items: center;
    width:20%;
}
nav .down .logo img{
    width:80px;
    height:80px;
    height:100%;
    object-fit: cover;
} 
nav .down .logo p{
    font-size: 28px;
    font-weight: 600;
    font-family: whisper;
    
} 
nav .down .links{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width:80%;
}
nav .down .links a{
   margin: 0px 50px;
   font-size: 22px;
}
nav .down .cart{
    width:40px;
    height:40px;
    
}
nav .down .icon{
    display: none;
}

a[class="active"] {
    color:#EC407A !important;
}
@media(min-width:1175px)and (max-width:1330px){
    nav .up{
        width:90%;
    }
    nav .down{
        width:90%;
    }

}
@media(min-width:950px)and (max-width:1170px){
    nav .up{
        width:90%;
    }
    nav .down{
        width:90%;
    }
    nav .down .links a{
        margin: 0px 30px;
        font-size: 22px;
}
nav .down .logo{
        width:25%;
}
}
@media(min-width:850px)and (max-width:949px){
nav .up{
        width:95%;
    }
nav .down{
        width:95%;
    }
nav .down .links a{
        margin: 0px 25px;
        font-size: 20px;
}
nav .down .logo{
        width:28%;
}
}

@media(min-width:350px)and (max-width:849px){
    nav .up{
        width:100%;
    }
    nav .up .right{
        width:50%;
    }
    nav .up .call{
        width:50%;
        margin-left: 0;
    }
    
    nav .down .links{
        position: fixed;
        position: absolute;
        top:125px;
        width:100%;
        height:80%;
        z-index:999; 
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color:bisque;
    }
    nav .down .links a{
        margin: 35px 0px;
    }

    .navopen{
        left:0;
        transition: 2.2s cubic-bezier(.36,-0.01,0,.77);        

        
    }
    .navclose{
        left:-850px;
        transition: 2.2s cubic-bezier(.36,-0.01,0,.77);        

    }
    nav .down{
        width:100%;
    }
    nav .down .logo{
        width:50%;
    }
nav .down .icon{
    position:absolute;
    right:70px;
    top:70px;
    width:40px;
    height:30px;
    display: block;
    cursor: pointer;
}
@media(max-width:550px){
    nav .down .icon{
        position:absolute;
        right:30px;

    }

}
nav .down .icon span{
    display: block;
    width: 100%;
    margin: 5px 0px;
    height:4px;
    background-color: #EC407A;
}









}
