*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.parent{
    width:100%;
    height:150px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.parent .child img{
    width:40px;
    height:40px;
}
.parent .child{
    /* width: 200px; */
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.5px solid rgba(0, 0, 0, 0.171);
    padding: 7px;
    
}
.parent .child .words{
    margin: 0px 5px;
    padding-bottom: 10px;

    color:rgb(39, 35, 35);
    font-family: Arial, Helvetica, sans-serif;
}
.parent .child .words h3{
    padding-bottom: 5px;
}
@media(min-width:515px)and (max-width:850px){

    .parent{
        height:180px;
        display: flex;
        flex-wrap: wrap;
    }
    .child{
        width:45%;
    }

}
@media(min-width:400px)and (max-width:514px){

    .parent{
        height:350px;
        display: flex;
        flex-wrap: wrap;
    }
    .child{
        width:90%;
    }

}



