*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.parent{
    width:100%;
    height:600px;
    position: relative;

}
.parent img{
    width:100%;
    height:600px;
}
.parent .words{
    position: absolute;
    color:whitesmoke;
    top:30%;
    left:5%;
    width:450px;
    height:250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
 
}



.parent .words .btn{
    border:2px solid #EC407A;
    color: #EC407A;
    padding: 10px;
    font-size: 20px;
    background-color: transparent;
    border-radius: 20px;
    width:150px;
    cursor: pointer;
  
}

.parent .words .btn:hover{
    background-color: #EC407A;
    color: white;
    transition: 0.8s;
}
.parent .words h1{
    font-size: 50px;
}
.parent .words p{
    font-size: 18px;
}

.parent .dots{
    position: absolute;
    top:95%;
    display: flex;
    width:100%;
    justify-content: center;
    align-items: center;
}
.parent .dots span{
    width:20px;
    height:20px;
    border-radius: 50%;
    background-color: #EC407A;
    display: flex;
    margin: 0px 2.5px;
    cursor: pointer;
}
.parent .dots span:hover{
    background-color: white;
    transition: 0.8s;
    border:none;

}
@media(min-width:400px)and (max-width:500px){
    .parent .words{
        width:350px;
        height:250px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
    }
    .parent .words h1{
        font-size: 50px;
    }
    .parent .words p{
        font-size: 17px;
        padding-bottom: 20px;
    }

}



