*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.parent{
    width:90%;
    margin: auto;
    height:400px;
    display: flex;
    flex-direction: column;
}
.parent h2{
    display: flex;
    width:40%;
    height:70px;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    font-style: italic;
    background-color: #EC407A;
    color:white
}
.parent .form{
    width:100%;
    height:70%;
    margin: auto;
    display: flex;
    flex-direction: column;
}
.parent .form .col label{
    color:#EC407A;
    font-weight: 700;
}
.parent .form .col{
    margin-top:30px ;
    font-size: 20px;
}
.parent .form input{
    width:300px;
    border: none;
    background-color: transparent;
    margin-left: 20px;
    letter-spacing: 1px;
    font-size: 18px;
}
.parent .form input::placeholder{

    border: 2px solid #EC407A;

}

@media(min-width:400px) and (max-width:530px){
    .parent{
        width:100%;
    }
    .parent .form{
        width:100%;
    }
    .parent .form input{
        width:200px;
       
    }


}