.parent{
    height:auto;
    width:90%;
    margin: auto;

}

.parent table{
    width:90%;
}
.parent table th{
    background-color: #EC407A;
    padding: 10px;
    color: white;
}
.parent table tbody img{
    width:200px;
    height:200px;

}
.parent .empty{
    width:160%;
    height:200px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 30px;
    font-style: italic;
    font-weight: 600;
}
.parent .empty img{
    margin-left: 10px;
    width:30px;
    height:30px;
}