*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
.parent {
    height:300px;
    width:100%;
    /* background-color: aqua; */
    display: flex;
    justify-content: center;
    
}
.parent .wrong{
    position:absolute;
    top:120px;
    font-weight: 600;
    font-size: 20px;
    color:rgb(226, 49, 49);

} 
.parent .child{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* background-color: bisque; */
    width:400px;
    position: relative;
}
.parent .child h2{
    position: absolute;
    top:10px;
    left:20px;
    font-style: italic;
    font-size: 30px;
}
.parent .child .col{
    margin: 7px 0px;
    display: flex;
    flex-direction: column;
}
.parent .child .col label{
    display: flex;
    width:200px;
    margin: 7px 0px;
    font-size: 18px;
}
.parent .child .col input{
    width:300px;
    height:30px;
    font-size: 14px;
    border: none;
    outline: none !important;
    padding: 5px;
    background-color: whitesmoke;
}
.parent .child .col input:focus{
    border:2px solid #EC407A;

}
.parent .child button{
    width:300px;
    border: none;
    background-color: #EC407A;
    height:30px;
    color:white;
    font-size: 14px;
    cursor: pointer;
    margin-top: 4px;
}
